body {
  font-family: 'Open Sans', sans-serif;
}
#mainLogo {
  width: 280px;
}
.appContainer {
  text-align: center;
  width: 100%;
}
.app-header {
  margin-top: 25px;
  margin-bottom: 30px;
}
.app-header h1 {
  font-family: 'Bad Script', cursive;
  margin-top: -50px;
}
.script {
  font-family: 'Bad Script', cursive;
}
#banner {
  margin-bottom: 40px;
}
#banner a img {
  width: 90%;
}
#video img {
  width: 100%;
}
#mainContent {
  text-align: left;
  padding: 1em;
}
#mainContent ul li {
  text-decoration: none;
  list-style: none;
}
#slideshow {
  text-align: center;
}
#contact .script {
  text-align: left;
  margin-left: 22px;
}
#contactContainer #contact ul {
  text-align: left;
  list-style-type: none;
  margin-top: 25px;
}
#contactContainer #social svg {
  width: 40px;
  padding: 10px;
}
#contactContainer #social #kickLogo {
  width: 35px;
}
#contactContainer #social a {
  text-decoration: none;
  color: #000;
}
#contactContainer #social svg:hover {
  color: #999;
}
#contactContainer {
  display: flex;
}
#contact {
  flex-grow: 1;
  padding-bottom: 25px;
}
#social {
  display: flex;
  align-items: center;
  margin-right: 20px;
  margin-top: 38px;
}
@media screen and (min-width: 768px) {
    #mainContent {
      display: flex;
      justify-content: space-evenly;
    }
    #aboutTxt {
      padding: 0 1em 0 .5em;
    }
    #slideshow img {
      margin-top: 40%;
    }

}
@media screen and (min-width: 768px) {
  .appContainer {
    width: 90%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1280px) {
  .appContainer {
    width: 80%;
    margin: 0 auto;
  }

}
